.shoetable {
    width: 100%;
    border-collapse: collapse;
    margin: 48px 0;
}

.shoetable th,
.shoetable td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
    font-size: 24px;
}

.shoetable th {
    background-color: #f4f4f4;
}

.nav-button {
    position: absolute;
    top: 80px;
    right: 20px;
    border: none;
    background: none;
    text-decoration: underline;
    color: blue;
    cursor: pointer;
    font-size: 20px;
}

.nav-button:hover {
    color: purple;
}

.add-button,
.save-button,
.remove-button,
.print-button,
.cancel-button {
    background-color: #28a745;
    color: white;
    padding: 10px 36px;
    border: none;
    cursor: pointer;
    margin: 10px;
    font-size: 24px;
}

.add-button:hover {
    background-color: #218838;
}

.save-button {
    background-color: #475ee3;
}

.save-button:hover {
    background-color: #5621c0;
}

.remove-button,
.cancel-button {
    background-color: red;
}

.remove-button:hover,
.cancel-button:hover {
    background-color: #b42c29;
}

.print-button {
    background-color: #555;
}

.print-button:hover {
    background-color: #333;
}

input,
select {
    padding: 5px;
    box-sizing: border-box;
    font-size: 24px;
}

h1 {
    font-size: 36px;
}

h3 {
    font-size: 24px;
}

h1,
h2,
h3 {
    margin: 0;
    color: red;
}

.summaryCell {
    font-weight: bold;
    border: none !important;
    padding-top: 24px !important;
    padding-bottom: 0 !important;
}

.suppliersInput {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 48px;
    font-size: 24px;
    gap: 1vw;
}

.suppliersInput input {
    width: 250px;
}

/* ===================== */
/* PRINTING STYLES */
/* ===================== */
@media print {

    /* Set print page size to A4 landscape */
    @page {
        size: A4 landscape;
        margin: 20mm;
    }

    /* Hide buttons when printing */
    .add-button,
    .save-button,
    .remove-button,
    .print-button {
        display: none !important;
    }

    /* Make inputs look like plain text */
    input,
    select {
        border: none !important;
        background: none !important;
        padding: 0 !important;
        font-size: inherit !important;
        width: auto !important;
        appearance: none !important;
        text-align: center !important;
    }

    /* Prevent text from being cut off */
    .shoetable {
        page-break-inside: avoid;
    }

    .totalPriceRow {
        visibility: hidden;
    }

    .summaryCell {
        border-top: 1px solid black !important;
    }
}