.warehouse-container {
    padding: 20px;
    text-align: center;
    margin: auto;
    background: white;
}

.filters {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 4vw;
    width: 100vw;
    margin: 24px 0;
    flex-wrap: wrap;
}

.filters input,
.filters select {
    padding: 8px;
    font-size: 120%;
    width: 20%;
}

.filters label {
    display: flex;
    align-items: center;
    font-size: 120%;
}

.warehouse-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    background: white;
    border-radius: 10px;
    overflow: hidden;
}

.warehouse-table th,
.warehouse-table td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: center;
    font-size: 120%;
}

.warehouse-table th {
    background: #f4f4f4;
    font-size: 120%;
    text-transform: uppercase;
}

.low-stock {
    background-color: #ffcccb;
    font-weight: bold;
    color: red;
}

.logout-button {
    position: absolute;
    top: 80px;
    right: 10vw;
    border: none;
    background: none;
    text-decoration: underline;
    color: red;
    cursor: pointer;
    font-size: 20px;
}

.logout-button:hover {
    color: darkred;
}