.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-field {
    display: flex;
    flex-direction: row;
    margin-top: 3vw;
    margin-bottom: 1vw;
    gap: 1vw;
    align-items: center;
}

.popup-field select {
    width: auto;
}

.popup-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 5vw;
}