.firstContainer {
    display: flex;
    flex-direction: row;
    width: 80vw;
    justify-content: space-between;
    gap: 4vw;
    padding: 10vw;
    background: white;
}

.home-button {
    cursor: pointer;
    width: 17vw;
    height: 17vw;
    font-size: 28px;
    background-color: #5Bc1f6;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-button button {
    width: 17vw;
    height: 4.25vw;
    font-size: 24px;
    color: white;
}

.home-button button.creation {
    background-color: #28a745;
}

.home-button button.creation:hover {
    background-color: #218838;
}

.home-button button.deletion {
    background-color: red;
}

.home-button button.deletion:hover {
    background-color: #b42c29;
}