.popup-content {
    background: white;
    padding: 3vw 5vw;
    border-radius: 1vw;
    font-size: 24px;
    width: 60vw;
}

.popup-overlay select {
    border: 1px solid #ccc;
    border-radius: 6px;
    cursor: pointer;
}

.popup-overlay select:focus {
    border-color: #007bff;
    outline: none;
}


@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.popup-overlay form input,
.number-pad-container {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 24px;
    margin-top: 3vw;
    margin-bottom: 1vw;
}