.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f8f8;
}

.login-box {
    background: #ffffff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 350px;
}

h2 {
    margin-bottom: 5px;
    font-size: 28px;
}

.subtext {
    font-size: 1em;
    color: #666;
}

.input-group input {
    width: 100%;
    padding: 10px;
    font-size: 1.25em;
    text-align: center;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f3f3f3;
}

.number-pad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin: 15px 0;
}

.num-btn {
    width: 100%;
    padding: 15px;
    font-size: 1.5em;
    text-align: center;
    border: none;
    border-radius: 10px;
    background: #e0e0e0;
    cursor: pointer;
    transition: background 0.2s;
}

.num-btn:hover {
    background: #d1d1d1;
}

.backspace {
    background: #ff6666;
    color: white;
}

.backspace:hover {
    background: #e05a5a;
}

.clear {
    background: #6666ff;
    color: white;
}

.clear:hover {
    background: #5a5ae0;
}

.login-button {
    width: 100%;
    padding: 12px;
    font-size: 1.25em;
    border: none;
    border-radius: 5px;
    background: black;
    color: white;
    cursor: pointer;
    transition: background 0.2s;
}

.login-button:hover {
    background: #333;
}

.error-message {
    color: red;
    font-size: 1em;
    margin-bottom: 10px;
}